.landing-wrapper {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(to bottom right, #f0f9ff, #e0f2fe);
  padding: 2rem;
}

.landing-box {
  max-width: 800px;
  width: 100%;
  background: white;
  padding: 3rem;
  border-radius: 16px;
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.main-title {
  font-size: 2.8rem;
  color: #1e3a8a;
  margin-bottom: 1rem;
}

.subtitle {
  font-size: 1.2rem;
  color: #334155;
  margin-bottom: 2rem;
}

.feature-section h2 {
  font-size: 1.5rem;
  color: #1e40af;
  margin-bottom: 1rem;
}

.features {
  list-style: none;
  padding: 0;
  text-align: left;
  margin-bottom: 2rem;
}

.features li {
  margin-bottom: 0.8rem;
  font-size: 1rem;
  color: #374151;
}

.cta-buttons {
  display: flex;
  gap: 1rem;
  justify-content: center;
  flex-wrap: wrap;
}

.btn {
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  text-decoration: none;
  font-weight: bold;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.btn.primary {
  background-color: #2563eb;
  color: white;
}

.btn.primary:hover {
  background-color: #1d4ed8;
}

.btn.secondary {
  background-color: white;
  color: #2563eb;
  border: 2px solid #2563eb;
}

.btn.secondary:hover {
  background-color: #eff6ff;
}
